"use client";
import React, { useRef } from "react";
import Link from "next/link";
import Header from "./Header";

const Navbar = () => {
  const menu = useRef();

  const menuhandler = () => {
    menu.current.classList.toggle("left-0");        // Show the menu
    menu.current.classList.toggle("left-[-100%]");  // Hide the menu
  };

  return (
    <div className="flex items-center justify-between px-6 py-6 relative bg-green-900">
      <div className="mr-4">
        <h1 className="font-bold text-2xl cursor-pointer text-gray-950">
          Guide-<span className="text-white text-2xl font-bold">U</span>
        </h1>
      </div>

      <div
        ref={menu}
        className="absolute top-[100%] left-[-100%] transition-all duration-300 ease-in-out
        md:static md:flex md:flex-row gap-y-4 md:gap-x-10 items-center flex-col
        lg:bg-transparent md:w-auto w-[200px] p-4 md:p-0 bg-green-900 z-50"
      >
        <Header name="Home" url="#Home" />
        <Header name="Category" url="#Category" />
        <Header name="Popular" url="#Popular" />
        <Header name="Shop" url="#Shop" />
        <Header name="Testimonials" url="#Testimonials" />
        <Header name="Contact" url="#Contact" />
      </div>

      <div className="flex items-center space-x-4">
        <button className="bg-green-500 p-2.5 rounded-[15px] hover:bg-white">
          Login
        </button>
        <i
          className="ri-menu-3-line text-white text-2xl md:hidden cursor-pointer"
          onClick={menuhandler}
        ></i>
      </div>
    </div>
  );
};

export default Navbar;
